@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
body{margin:0;padding:0;border:0;font-size:100%;font-family:Montserrat,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#121212;color:#fff}a{color:inherit;text-decoration:none}header{display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:700;margin-top:10em}header h1{font-size:5em}.socials{display:flex;flex-direction:row;justify-content:center;align-items:center;padding:30px;font-weight:500;font-size:40px}.social-link{display:flex;flex-direction:row;justify-content:center;align-items:center;margin-left:20px;margin-right:20px;transition:all .25s ease}.social-link:hover{color:gray}.social-text{padding-left:5px}
